export default {
	 onLaunch: function() { 
	    // 获取屏幕高度
		
	    wx.getSystemInfo({
	      success: function(res) {
			  console.log(res)
			  console.log('屏幕高度' + res.windowHeight)
			  console.log('窗口高度' + res.screenHeight)
			  console.log('状态栏高度' + res.statusBarHeight)
	      }
	    })
	}
}
